import React from 'react';


const About = () => {
  return (
    <div>
      <h2>Hello, friend</h2>
      <p>This place is a place, a representation of a place. It is the essence and substance from whence you came and to where henceforce you are destined to tread. Stay! Stay!</p>
    </div>
    )
}

export default About


